  display: flex;
  order: 1;
}
.wadi_info_box_container.wadi_media_above-content .wadi_info_box_content {
  order: 2;
}
.wadi_info_box_container.wadi_media_above-content .wadi_info_box_cta {
  order: 3;
}
.wadi_info_box_container.wadi_media_below-content {
  flex-direction: column;
}
.wadi_info_box_container.wadi_media_below-content .wadi_info_box_content {
  order: 1;
}
.wadi_info_box_container.wadi_media_below-content .wadi_info_box_graphic_media {
  display: flex;
  order: 2;
}
.wadi_info_box_container.wadi_media_below-content .wadi_info_box_cta {
  order: 3;
}
.wadi_info_box_container.wadi_media_left-content {
  flex-direction: row;
}
.wadi_info_box_container.wadi_media_left-content .wadi_info_box_graphic_media {
  display: flex;
  order: 1;
}
.wadi_info_box_container.wadi_media_left-content .wadi_info_box_content {
  order: 2;
}
.wadi_info_box_container.wadi_media_left-content .wadi_info_box_cta {
