  order: 1;
}
.wadi_info_box_container.wadi_media_right-content .wadi_info_box_graphic_media {
  display: flex;
  order: 2;
}
.wadi_info_box_container.wadi_media_right-content .wadi_info_box_cta {
  order: 3;
}
.wadi_info_box_container .wadi_info_box_media_image, .wadi_info_box_container .wadi_info_box_icon {
  display: inline-block;
  line-height: 0;
  position: relative;
  max-width: 100%;
